.AppCont {
  text-align: center;
  justify-content: center;
 

  width:350px;
  height: 110vh;
 margin:0 auto;
}



 .appHeader {
  background-color:#3D4257;
  align-items: center;
  color: white;
  margin-top: 0;
} 



ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: 1px solid #e7e7e7;
  background-color: #f3f3f3;
}

li {
  float: left;
}

li a {
  display: block;
  color: #666;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #ddd;
}

li a.active {
  color: white;
  background-color: #4CAF50;
}
nav{
  width: 100%;
  /* background-color: #555; */
  overflow: auto;
  color:#3D4257 ;
}
/* .navbar {
  width: 100%;
  background-color: #555;
  overflow: auto;
} */


.informationButton{

  display: inline-block;
  width: 90%;
  padding: 15px 25px;

  font-size: 16px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color:#3D4257;
  border: none;
  border-radius: 15px;
 
}
.informationButton:hover {background-color: #262833}


.info{
  padding-top: 15px;
}


.directorInfo{
    background-color:#3D4257;
    text-align: center;
    margin: 0%;
    margin-top: 0;
    padding: 10npx;
  
}
.directorInfo button{

    display: inline-block;
    width: 100%;
    padding: 10px 20px;
  
    font-size: 12px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color:#3D4257;
    border: none;
    border-radius: 15px;
}
.directorInfo img{
  object-fit: cover;
  border-radius:50%;
  width: 60px;
  height: 60px;
  margin: 5%;
  border: 1px solid white;
}


.News{
    text-align: right;
    color:#3D4257 ;
}

hr.new1{
    border: 1px solid #3D4257;
  }

.summaryD{
    text-align: right;
    color:#3D4257 ;
    display: grid;
  grid-template-columns: 30% 70%;
}
p{
    font-size: 14px;
    
}


.backButton{

    display: inline-block;
  width: 90%;
  padding: 10px 20px;
  margin: 10px;

  font-size: 12px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color:#283157;
  border: none;
  border-radius: 15px;
 

}

